import React, { useState } from 'react';
import PropTypes from 'prop-types';
import {
  StyledIngredientItem,
  IngredientImageWrapper,
  IngredeintName,
  IngredientQuantity,
  DeleteIcon,
  EditIcon,
} from './IngredientItem.style';
import { motion, AnimatePresence } from 'framer-motion';
import { ReactComponent as EditIconSVG } from 'assets/icons/editIcon.svg';

const IngredientItem = ({
  modeTemp,
  deleteTemp,
  editFunction,
  data: {
    ingredientName,
    ingredientImagePath,
    ingredientQuantity,
    ingredientUnit,
    ingredientNameExtended,
  },
}) => {
  const [isTooltipOpen, setIsTooltipOpen] = useState(false);
  const toggleTooltip = () => {
    setIsTooltipOpen((prevState) => !prevState);
  };

  const deleteTemps = (e) => {
    const targetName = e.target.dataset.name;
    deleteTemp(targetName);
  };
  return (
    <StyledIngredientItem
      isEditOn={modeTemp}
      as={motion.li}
      layout
      key={ingredientName}
      onClick={toggleTooltip}
    >
      <IngredientImageWrapper isEditOn={modeTemp}>
        <AnimatePresence>
          {modeTemp ? (
            <>
              <DeleteIcon
                initial={{ x: -20, rotate: '45deg' }}
                animate={{ x: 0, rotate: '45deg' }}
                exit={{ x: 20 }}
                as={motion.span}
                onClick={deleteTemps}
                data-name={`${ingredientName}`}
              >
                +
              </DeleteIcon>
              <EditIcon
                initial={{ x: -20 }}
                animate={{ x: 0 }}
                exit={{ x: 20 }}
                as={motion.span}
                onClick={editFunction}
              >
                <EditIconSVG />
              </EditIcon>
            </>
          ) : (
            <img src={ingredientImagePath} />
          )}
        </AnimatePresence>
      </IngredientImageWrapper>

      <IngredeintName isTooltipOpen={isTooltipOpen}>
        {ingredientName}
      </IngredeintName>
      <IngredeintName isTooltipOpen={isTooltipOpen} isToolTip={true}>
        {ingredientNameExtended ? ingredientNameExtended : 'Brak szczegółów'}
      </IngredeintName>

      <IngredientQuantity>{`${ingredientQuantity} ${ingredientUnit}`}</IngredientQuantity>
    </StyledIngredientItem>
  );
};

IngredientItem.propTypes = {};

export default IngredientItem;
